import React from 'react';
import { Box } from '@material-ui/core';

interface ISRULogoProps {
  width?: number;
  height?: number;
  className?: string;
  animated?: boolean;
}

export const ISRULogo: React.FC<ISRULogoProps> = ({ 
  width = 200, 
  height = 120, 
  className,
  animated = true 
}) => {
  return (
    <Box className={className} style={{ position: 'relative' }}>
      <svg
        width={width}
        height={height}
        viewBox="0 0 400 240"
        xmlns="http://www.w3.org/2000/svg"
        style={{ filter: 'drop-shadow(0 0 10px rgba(255, 107, 53, 0.3))' }}
      >
        {/* Gradient Definitions */}
        <defs>
          <linearGradient id="isruGradient" x1="0%" y1="0%" x2="100%" y2="0%">
            <stop offset="0%" stopColor="#ff6b35" />
            <stop offset="50%" stopColor="#FFD700" />
            <stop offset="100%" stopColor="#ff6b35" />
          </linearGradient>
          
          <linearGradient id="spaceGradient" x1="0%" y1="0%" x2="100%" y2="100%">
            <stop offset="0%" stopColor="#1a1a2e" />
            <stop offset="50%" stopColor="#16213e" />
            <stop offset="100%" stopColor="#0f3460" />
          </linearGradient>
          
          <radialGradient id="planetGradient" cx="50%" cy="50%" r="50%">
            <stop offset="0%" stopColor="#ff8c5a" />
            <stop offset="70%" stopColor="#ff6b35" />
            <stop offset="100%" stopColor="#d73027" />
          </radialGradient>

          {animated && (
            <animateTransform
              attributeName="transform"
              attributeType="XML"
              type="rotate"
              from="0 50 50"
              to="360 50 50"
              dur="20s"
              repeatCount="indefinite"
            />
          )}
        </defs>

        {/* Background Space Circle */}
        <circle
          cx="200"
          cy="120"
          r="110"
          fill="url(#spaceGradient)"
          stroke="url(#isruGradient)"
          strokeWidth="3"
          opacity="0.8"
        />

        {/* Mars Planet */}
        <circle
          cx="320"
          cy="80"
          r="25"
          fill="url(#planetGradient)"
          opacity="0.9"
        >
          {animated && (
            <animateTransform
              attributeName="transform"
              attributeType="XML"
              type="rotate"
              from="0 320 80"
              to="360 320 80"
              dur="15s"
              repeatCount="indefinite"
            />
          )}
        </circle>

        {/* Orbital Ring */}
        <circle
          cx="200"
          cy="120"
          r="80"
          fill="none"
          stroke="url(#isruGradient)"
          strokeWidth="2"
          strokeDasharray="8,4"
          opacity="0.6"
        >
          {animated && (
            <animateTransform
              attributeName="transform"
              attributeType="XML"
              type="rotate"
              from="0 200 120"
              to="360 200 120"
              dur="30s"
              repeatCount="indefinite"
            />
          )}
        </circle>

        {/* Central Mining Station */}
        <g transform="translate(200, 120)">
          {/* Main Structure */}
          <rect
            x="-20"
            y="-15"
            width="40"
            height="30"
            fill="url(#isruGradient)"
            stroke="#FFD700"
            strokeWidth="2"
            rx="5"
          />
          
          {/* Mining Arms */}
          <line x1="-30" y1="0" x2="-20" y2="0" stroke="#FFD700" strokeWidth="3" strokeLinecap="round" />
          <line x1="20" y1="0" x2="30" y2="0" stroke="#FFD700" strokeWidth="3" strokeLinecap="round" />
          
          {/* Antenna */}
          <line x1="0" y1="-15" x2="0" y2="-25" stroke="#ff6b35" strokeWidth="2" strokeLinecap="round" />
          <circle cx="0" cy="-25" r="3" fill="#FFD700" />
          
          {/* Solar Panels */}
          <rect x="-40" y="-8" width="15" height="16" fill="#4a90e2" opacity="0.8" rx="2" />
          <rect x="25" y="-8" width="15" height="16" fill="#4a90e2" opacity="0.8" rx="2" />
        </g>

        {/* Stars */}
        <circle cx="80" cy="60" r="2" fill="#FFD700" opacity="0.8">
          {animated && (
            <animate attributeName="opacity" values="0.8;0.3;0.8" dur="3s" repeatCount="indefinite" />
          )}
        </circle>
        <circle cx="120" cy="40" r="1.5" fill="#ffffff" opacity="0.6">
          {animated && (
            <animate attributeName="opacity" values="0.6;0.2;0.6" dur="2s" repeatCount="indefinite" />
          )}
        </circle>
        <circle cx="350" cy="180" r="2" fill="#ff6b35" opacity="0.7">
          {animated && (
            <animate attributeName="opacity" values="0.7;0.3;0.7" dur="4s" repeatCount="indefinite" />
          )}
        </circle>

        {/* I.S.R.U Text */}
        <text
          x="200"
          y="180"
          textAnchor="middle"
          fill="url(#isruGradient)"
          fontSize="32"
          fontFamily="'Rocket Sharpie Bold', 'Arial Black', monospace"
          fontWeight="bold"
          letterSpacing="4px"
        >
          I.S.R.U
        </text>

        {/* League Subtitle */}
        <text
          x="200"
          y="200"
          textAnchor="middle"
          fill="#FFD700"
          fontSize="14"
          fontFamily="'Arial', sans-serif"
          fontWeight="600"
          letterSpacing="2px"
        >
          LEAGUE
        </text>

        {/* Resource Particles */}
        {animated && (
          <>
            <circle cx="150" cy="100" r="1" fill="#FFD700">
              <animateMotion dur="6s" repeatCount="indefinite">
                <path d="M 0 0 Q 25 -10 50 0 Q 75 10 100 0" />
              </animateMotion>
            </circle>
            <circle cx="250" cy="140" r="1" fill="#ff6b35">
              <animateMotion dur="8s" repeatCount="indefinite">
                <path d="M 0 0 Q -25 10 -50 0 Q -75 -10 -100 0" />
              </animateMotion>
            </circle>
          </>
        )}
      </svg>
    </Box>
  );
};

export default ISRULogo;
